import Vue from 'vue'
import Router from 'vue-router'
import store from '../store.js'
const Logout = () => import('../components/Logout.vue')

function loadViewDashboard (view) {
  return () => import(/* webpackChunkName: "viewDashboard-[request]" */ `../components/part/${view}.vue`)
}
function loadViewLayout (view) {
  return () => import(/* webpackChunkName: "viewLayout-[request]" */ `../components/layout/${view}.vue`)
}
const routes = [
  {
    path: '/',
    name: 'Login',
    component: loadViewLayout('Login')
  },
  {
    path: '/scanQr/:qrcode',
    name: 'scanQr',
    component: loadViewLayout('scanQr')
  },
  {
    path: '/daftarPasien',
    name: 'daftarPasien',
    component: loadViewLayout('daftarPasien')
  },
  {
    path: '/dashboard',
    component: loadViewLayout('Dashboard'),
    meta: { requiresAuth: true },
    children: [
      {
        path: '',
        name: 'DashboardContent',
        component: loadViewDashboard('mainView')
      },
      {
        path: 'ubahpassword',
        name: 'changePassword',
        component: loadViewDashboard('changePassword')
      },
      {
        path: 'saranDokter',
        name: 'saranDokter',
        component: loadViewDashboard('saranDokter')
      },
      {
        path: 'dokter',
        name: 'dokter',
        component: loadViewDashboard('Dokter')
      },
      {
        path: 'patientQueue',
        name: 'patientQueue',
        component: loadViewDashboard('patientQueue')
      },
      {
        path: 'patien',
        name: 'patien',
        component: loadViewDashboard('userData')
      }
    ]
  },
  {
    path: '/logout',
    name: 'Logout',
    component: Logout
  }
]
Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: routes,
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }})
router.beforeEach((to, from, next) => {
  if (to.matched.some(route => route.meta.requiresAuth) && !store.state.isLoggedIn) {
    next({ name: 'Login' })
    return
  }
  // if logged in redirect to dashboard
  if (to.name === 'Login' && store.state.isLoggedIn) {
    next({ name: 'DashboardContent' })
    return
  }
  // if logged in redirect to dashboard
  if (to.name === 'DashboardContent' && !store.state.isLoggedIn) {
    next({ name: 'Login' })
    return
  }
  next()
})
export default router



